import * as React from 'react';
import { useStorage } from '../../utils';
import CheckboxForm from './_CheckboxForm';

export default function QFreeVac() {
  const { isMale } = useStorage();
  const checkboxes = getCheckboxes(isMale);

  return (
    <CheckboxForm
      checkboxes={checkboxes}
      prevPage="../q-dob-sex"
      nextPageForChecked="../q-existing-vac"
      nextPageForNothing="../q-need-vac"
    />
  );
}

function getCheckboxes(isMale: boolean) {
  return [
    isMale && 'Я призывник',
    'У меня есть заболевание легких',
    `Я пенсионер${isMale ? '' : 'ка'} и живу в интернате / доме престарелых и т. п.`,
  ].filter(Boolean);
}
