import * as React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormGroup from '@mui/material/FormGroup';
import Checkbox from '@mui/material/Checkbox';
import { navigate } from 'gatsby';
import { useState } from 'react';
import PrevNextButtons from './_PrevNextButtons';

export interface CheckboxFormProps {
  checkboxes: string[];
  prevPage: string;
  /** страница если выбран хотя бы один вариант */
  nextPageForChecked: string;
  /** страница если ничего не выбрано */
  nextPageForNothing: string;
};

export type CheckedState = 'initial' // начальное состояние: все чекбоксы активны
  | 'checked' // есть выбранные чекбоксы (кроме "ничего из вышеперечисленного")
  | 'nothing'; // выбрано "ничего из вышеперечисленного"

/**
 * Форма, которая рисует чекбоксы и добавляет чекбокс "Ничего из вышеперечисленного".
 */
export default function CheckboxForm(props: CheckboxFormProps) {
  const [ checkedState, setCheckedState ] = useState<CheckedState>('initial');
  const [ nextEnabled, setNextEnabled ] = useState(false);

  const handleNext = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const state = getCheckedState(event.currentTarget);
    if (state === 'checked') navigate(props.nextPageForChecked);
    if (state === 'nothing') navigate(props.nextPageForNothing);
  };

  const handleChange = (event: React.FormEvent<HTMLFormElement>) => {
    const state = getCheckedState(event.currentTarget);
    setCheckedState(state);
    setNextEnabled(state !== 'initial');
  };

  return (
    <form onSubmit={handleNext} onChange={handleChange}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Typography variant="h5" sx={{ mt: 2 }}>
            Выберите справедливые утверждения
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <FormGroup>
            {props.checkboxes.map((label, index) => {
              return (
                <FormControlLabel
                disabled={checkedState === 'nothing'}
                control={<Checkbox />}
                key={index}
                label={label}
                sx={{ mb: 1 }}
                />
              );
            })}
              <FormControlLabel
                disabled={checkedState === 'checked'}
                control={<Checkbox />}
                label="Ничего из вышеперечисленного"
                sx={{ mb: 1 }}
              />
          </FormGroup>
        </Grid>
        <PrevNextButtons prevPage={props.prevPage} nextEnabled={nextEnabled}/>
      </Grid>
    </form>
  );
}

function getCheckedState(form: HTMLFormElement): CheckedState {
  const checkboxes = [ ...form.querySelectorAll('[type="checkbox"]') ];
  for (let i = 0; i < checkboxes.length; i++) {
    const el = checkboxes[i] as HTMLInputElement;
    if (el.checked && i < checkboxes.length - 1) return 'checked';
    if (el.checked && i === checkboxes.length - 1) return 'nothing';
  }
  return 'initial';
}

